const createList = (value) => {
    return createNode(value);
}
const appendList = (list, value) => {
    const node = createNode(value);
    let workList = list;
    while (workList.next) {
        workList = workList.next;
    }
    // æœ€åŽä¸€ä¸ªèŠ‚ç‚¹çš„next = ç›®æ ‡node
    workList.next = node;
    return node;
};
