const createNode = (value) => {
    return {
        data: value,
        next: null
    }
};
const travelList = (list, fn) => {
    let workList = list;
    while (workList !== null) {
        fn(workList);
        workList = workList.next;
    }
}
const getNode = (list, index) => {
    let x = list;
    while (x.data !== index) {
        x = x.next;
    }
    return x;
}

